import React, { useState } from 'react'
import ReplayIcon from '@material-ui/icons/Replay';
// import { NavLink } from 'react-router-dom'
// import Sound from './PlaySound'
import winf from './Images/win.mp3'
import clickf from './Images/click.mp3'
import Game from './Game'
import useSound from 'use-sound'
import { Hidden } from '@material-ui/core';
var cs = 8;
var ran = [0, 1, 2, 3, 4, 5, 6, 7, 8];
const XOX = () => {
	let array = ["", "", "", "", "", "", "", "", ""]
	const [arr, setarr] = useState(["", "", "", "", "", "", "", "", ""])
	const [char, setchar] = useState("X")
	const [playc] = useSound(clickf)
	const [playw] = useSound(winf)
	const [status, setStatus] = useState("turn")
	const [P, setP] = useState("1P")
	const [scoreX, setscoreX] = useState(0)
	const [scoreO, setscoreO] = useState(0)
	const [streakX, setstreakX] = useState("")
	const [streakO, setstreakO] = useState("")
	const [clrX, setclrX] = useState('')
	const [clrO, setclrO] = useState('')
	// const [music, setMusic] = useState(null)
	// const [musics,setMusics]=useState('Sound.status.PLAYING')

	var padd = "10px"
	if (streakX > 19 || streakO > 19) {
		padd = "6px"
	}
	else if (streakX > 9 || streakO > 9) {
		padd = "7px"
	}
	else if (streakX > 1 || streakO > 1) {
		padd = "9px"
	}
	// setMusic(null);
	const check = (newd) => {
		if (newd[0] && newd[1] && newd[2] && newd[0] === newd[1] && newd[1] === newd[2]) {
			setStatus("wins")
			playw();

		}
		else if (newd[3] && newd[4] && newd[5] && newd[3] === newd[4] && newd[4] === newd[5]) {
			setStatus("wins")
			playw();


		}
		else if (newd[6] && newd[7] && newd[8] && newd[6] === newd[7] && newd[7] === newd[8]) {
			setStatus("wins")
			playw();

		}
		else if (newd[0] && newd[3] && newd[6] && newd[0] === newd[3] && newd[3] === newd[6]) {
			setStatus("wins")
			playw();

		}
		else if (newd[1] && newd[4] && newd[7] && newd[1] === newd[4] && newd[4] === newd[7]) {
			setStatus("wins")
			playw();

		}
		else if (newd[2] && newd[5] && newd[8] && newd[2] === newd[5] && newd[5] === newd[8]) {
			setStatus("wins")
			playw();

		}
		else if (newd[0] && newd[1] && newd[2] && newd[0] === newd[1] && newd[1] === newd[2]) {
			setStatus("wins")
			playw();

		}
		else if (newd[0] && newd[4] && newd[8] && newd[0] === newd[4] && newd[4] === newd[8]) {
			setStatus("wins")
			playw();


		}
		else if (newd[2] && newd[4] && newd[6] && newd[2] === newd[4] && newd[4] === newd[6]) {
			setStatus("wins")
			playw();
		}
		else if (cs < 0) {
			setStatus("Draw")
			setchar("")
			return 1;
		}
		else {
			setchar((dat) => {
				if (dat === "X") {
					return "O"
				}
				else { return "X" }
			}); return 1
		}
		return 0
	}
	const changed = () => {
		replay()
		setscoreX(0)
		setscoreO(0)
		setstreakO("")
		setstreakX("")
		setchar("X")
		setclrX("transparent")
		setclrO("transparent")
		setP((dat) => {
			if (dat === "1P") {
				return "2P"
			}
			else { return "1P" }
		})
	}
	const clicked = (vals) => {
		// setMusic('click')
		playc();
		if (status !== 'turn') {
			replay()
		}
		if (!arr[vals.target.id] && status === "turn") {
			setarr((olddata) => {
				var newd = [...olddata]
				newd[vals.target.id] = char
				cs = cs - 1
				if (!check(newd)) {
					if (status) {
						if (char === "X") {
							setscoreX((val) => { return val + 1 })
							setstreakX((val) => { return val + 1 - 0 })
							setclrO("transparent")
							if (streakX < 3) { setclrX("coin bronze") }
							else if (streakX < 5) { setclrX("coin silver") }
							else { setclrX("coin gold") }
							setstreakO("")

						}
						else if (char === "O") {
							setscoreO((val) => { return val + 1 })
							setstreakO((val) => { return val + 1 - 0 })
							setstreakX("")
							setclrX("transparent")
							if (streakO < 3) { setclrO("coin bronze") }
							else if (streakO < 5) { setclrO("coin silver") }
							else { setclrO("coin gold") }
						}
					}
					return [...newd]
				}
				else {
					ran = ran.filter((valx) => { return valx != vals.target.id })
					if (P === "1P" && ran && status === "turn") {
						var randval = Math.floor(Math.random() * ran.length)
						newd[ran[randval]] = "O"
						cs = cs - 1
						if (!check(newd)) {
							setscoreO((val) => { return val + 1 })
							setstreakO((val) => { return val + 1 - 0 })
							setstreakX("")
							setclrX("transparent")
							if (streakO < 3) { setclrO("coin bronze") }
							else if (streakO < 5) { setclrO("coin silver") }
							else { setclrO("coin gold") }
						}
						ran.splice(randval, 1)
						return [...newd]
					}
					else { return [...newd] }
				}
			})
		}
	}
	const replay = () => {
		// setMusic('click')
		setarr([...array])
		if (P === "1P") { setchar("X") }
		else {
			setchar((val) => {
				if (val === "X") { return "O" }
				else { return "X" }
			})
		}
		setStatus("turn")
		cs = 8
		ran = [0, 1, 2, 3, 4, 5, 6, 7, 8]
	}
	return (<>
		<Game />
		<div className="main_div" style={{flexDirection:'column'}}>
		<div className="center_div" style={{
				margin: "20px",
				paddingLeft: '10px',
				height: "150px",
				width: "285px",
				display: "flex",
				flexDirection: 'column',
				justifyContent: "center",
				alignItems: 'left',
				textAlign: 'left',
				backgroundColor: 'transparent',
			}}>
				<div style={{
					display: "flex",
					flexDirection: 'row',
					justifyContent: 'left',
					alignItems: 'left',
					textAlign: 'left',
					margin: '0'
				}}>
					<div style={{
						display: "flex",
						flexDirection: 'column',
						justifyContent: 'center',
						alignItems: 'center',
						textAlign: 'center',
						backgroundColor: 'transparent',
						marginLeft: "5px"
					}}>
						<div style={{
							display: "flex",
							flexDirection: 'row',
							justifyContent: 'center',
							alignItems: 'center',
							textAlign: 'center',
							backgroundColor: 'transparent',
						}}>
							<h10 style={{ fontSize: '15px', marginRight: "3px" }}>1P</h10>
							<label className="switch" style={{ marginTop: '0px', marginLeft: '0px' }}>
								<input type="checkbox" onClick={changed} />
								<span className="slider round"></span>
							</label>
							<h10 style={{ fontSize: '15px', marginLeft: "3px" }}>2P</h10>
						</div>
						<button onClick={replay} className='button_three' style={{ margin: "5px" }}> <ReplayIcon /></button>
					</div>
					{(P === "1P" && status === 'turn') ?
						<span style={{
							padding: "30px",
							color: "blue",
							fontSize: '29px',
							display: "flex",
							flexDirection: 'center',
							justifyContent: "center",
							alignItems: 'center',
							textAlign: 'center',
							marginTop: "-30px",
							marginLeft: "20px",
							fontFamily: " 'Mate SC', serif"
						}}> {"Play!"}</span> : null}
					{(P === "1P" && status === 'wins' && char === "X") ?
						<span style={{
							padding: "30px",
							color: "blue",
							fontSize: '24px',
							display: "flex",
							flexDirection: 'center',
							justifyContent: "center",
							alignItems: 'center',
							textAlign: 'center',
							marginTop: "-30px",
							marginLeft: "-10px",
							fontFamily: " 'Mate SC', serif"
						}}> {"You wins"}</span> : null}
					{(P === "1P" && status === 'wins' && char === "O") ?
						<span style={{
							padding: "30px",
							color: "blue",
							fontSize: '27px',
							display: "flex",
							flexDirection: 'center',
							justifyContent: "center",
							alignItems: 'center',
							textAlign: 'center',
							marginTop: "-30px",
							marginLeft: "-7px",
							fontFamily: " 'Mate SC', serif"
						}}> {"Comp wins"}</span> : null}
					{(P === "1P" && status === 'Draw') ?
						<span style={{
							padding: "30px",
							color: "blue",
							fontSize: '29px',
							display: "flex",
							flexDirection: 'center',
							justifyContent: "center",
							alignItems: 'center',
							textAlign: 'center',
							marginTop: "-30px",
							marginLeft: "0px",
							fontFamily: " 'Mate SC', serif"
						}}> {"draw"}</span> : null}
					{(P === "2P") ?
						<span style={{
							padding: "30px",
							color: "blue",
							fontSize: '30px',
							display: "flex",
							flexDirection: 'center',
							justifyContent: "center",
							alignItems: 'center',
							textAlign: 'center',
							marginTop: "-30px",
							marginLeft: "-0px",
							fontFamily: " 'Mate SC', serif"
						}}> {char + " " + status}</span> : null}
				</div>
				{(P === "1P") ?
					<div style={{
						display: "flex",
						flexDirection: 'column',
						justifyContent: 'center',
						alignItems: 'center',
						textAlign: 'center', marginTop: '-20px'
					}}>
						<span style={{ color: 'green', fontSize: "25px", marginTop: "-5px" }}>Score</span>
						<span style={{ fontSize: "25px", marginTop: "-5px" }}>
							<span style={{ color: '#ff4137', fontFamily: " 'Mate SC', serif" }}>{"You: "}</span>
							<span >{scoreX}</span>
							{(streakX) ? <div className={clrX} style={{ marginTop: "-5px" }}><p style={{ paddingLeft: padd }}>{streakX}</p></div> : null}
							<span style={{ color: '#37e0ff', fontFamily: " 'Mate SC', serif", marginLeft: "40px" }}>{"Comp: "}</span>
							<span>{scoreO}</span>
							{(streakO) ? <div className={clrO} style={{ marginTop: "-5px" }}><p style={{ paddingLeft: padd }}>{streakO}</p></div> : null}
						</span>
					</div> :
					<div style={{
						display: "flex",
						flexDirection: 'column',
						justifyContent: 'center',
						alignItems: 'center',
						textAlign: 'center', marginTop: '-20px'
					}}>
						<span style={{ color: 'green', fontSize: "25px", marginTop: "-5px" }}>Score</span>
						<span style={{ fontSize: "25 px", marginTop: "-5px" }}>
							<span style={{ color: '#ff4137', fontFamily: " 'Mate SC', serif" }}>{"X: "}</span>
							<span>{scoreX}</span>
							{(streakX) ? <div className={clrX} style={{ marginTop: "-5px" }}><p style={{ paddingLeft: padd }}>{streakX}</p></div> : null}
							<span style={{ marginLeft: "40px", color: '#37e0ff', fontFamily: " 'Mate SC', serif" }}>{"O: "}</span>
							<span>{scoreO}</span>
							{(streakO) ? <div className={clrO} style={{ marginTop: "-5px" }}><p style={{ paddingLeft: padd }}>{streakO}</p></div> : null}
						</span>
					</div>}
			</div>
			<div className="gamer">
				<div className="center_div" style={{
				height: "285px",
					width: "285px",
					display: "flex",
					flexDirection: 'center',
					justifyContent: "center",
					alignItems: 'center',
					textAlign: 'center',
					backgroundColor: 'transparent',
					margin:"2px",
					// boxShadow:'0px 0px 0px 0px',
					overflow:"hidden",
					fontFamily: " 'Mate SC', serif"
				}}>
					<span></span><span></span><span></span><span></span>
				
					{/*<Sound music={music} />*/}
					<div className="grid">
						{arr.map((val, index) => {
							if (val === "X") {
								return (<div className="cell"><button id={index} className="XOXbutton" onClick={clicked} style={{ backgroundColor: '#ff4137' }}>{arr[index]}</button></div>);
							}
							else if (val === "O") {
								return <div className="cell"><button id={index} className="XOXbutton" onClick={clicked} style={{ backgroundColor: '#37e0ff' }}>{arr[index]}</button></div>;
							}
							else {
								return <div className="cell"><button id={index} className="XOXbutton" onClick={clicked}>{arr[index]}</button></div>;
							}
						})}
					</div>
				</div>
			</div>
			</div>
	</>
	)
}

export default XOX