import React,{useState} from 'react'
import axios from 'axios'
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import Joke from './Joke'
import Fact from './Fact'
import Temp from './Temp'
import Game from './Game'
const Quote= ()=>{
	const [data,setdata]=useState()
	const [ans,setans] = useState()
	const [cityad,setcityad] = useState()
	const [temp,settemp] = useState([])
	const getQuote =async ()=>{
			const res = await axios.get('https://type.fit/api/quotes')
			await setdata(res.data)
			
			
	}
	// console.log(data)
	if(!data){
	getQuote()}
	if(!ans && data){
		setans(data[Math.floor(Math.random()*data.length)])
	}
	const replay=()=>{
		setans(data[Math.floor(Math.random()*data.length)])	
	}
	const bgim=(val)=>{
		setcityad(val)
		// console.log(val)
	}
	const tempf=(val)=>{
		settemp(val)
		// console.log(val)
	}
	return (<>
		<Game/>
		<div style={{ backgroundImage: `url(${"" ? "white" : cityad})`,
	backgroundRepeat: 'no-repeat',
	backgroundAttachment: "fixed",
	backgroundSize: "cover",
	backgroundPosition: "center",
	}}>
		<div style={{minHeight:"100vh"}}>
		<div style={{display:'flex', flexDirection:"center", justifyContent:'center', color:'black'}}>
		<div className="api">
		<div className="center_div" style={{
			margin:  "20px",
			padding: '20px',
			height: "350px",
			width: "285px",
			display: "flex",
			flexDirection: 'column',
			justifyContent: "center",
			alignItems: 'left',
			textAlign: 'left',
			backgroundColor: 'rgba(207,226,243,0.8)',
		}}>
		<div style={{display:"flex", flexDirection:"column",alignItems:"center"}} >
		<Temp bgimg={bgim} ptemp={tempf}/></div>
		</div>
		<div className="center_div" style={{
			margin:  "20px",
			padding: '20px',
			height: "350px",
			width: "285px",
			display: "flex",
			flexDirection: 'column',
			justifyContent: "center",
			alignItems: 'left',
			textAlign: 'left',
			backgroundColor: 'rgba(180,255,180,0.8)',

		}}>
		{
		(data && ans) ? 
		<div style={{display:"flex", flexDirection:"column",alignItems:"center"}} >
		<span>{'"'+ans['text']+'"'}</span>
		<span>{'-'+ans['author']}</span>
		<button onClick={replay} className='button_four' style={{margin:"5px"}}> <NavigateNextIcon/></button></div>
		:"loading"
		}
		</div>
		<div className="center_div" style={{
			margin:  "20px",
			padding: '20px',
			height: "350px",
			width: "285px",
			display: "flex",
			flexDirection: 'column',
			justifyContent: "center",
			alignItems: 'left',
			textAlign: 'left',
			backgroundColor: 'rgba(234,209,220,0.8)',
		}}>
		<div style={{display:"flex", flexDirection:"column",alignItems:"center"}} >
		<Joke/></div>
		</div>
		<div className="center_div" style={{
			margin:  "20px",
			padding: '20px',
			// minHeight: "350px",
			// maxHeight: " 350px",
			height:"350px",
			width: "285px",
			display: "flex",
			justifyContent: "center",
			alignItems: 'left',
			textAlign: 'left',
			backgroundColor: 'rgba(255,229,153,0.8)',
		}}>
		<div style={{display:"flex", flexDirection:"column",alignItems:"center"}} >
		<Fact/></div>
		</div>
		</div>
		</div>
		</div>
		</div>
		</>
	)
	}

 export default Quote