import React,{useState,useEffect} from 'react'
import axios from 'axios'
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import GooglePlacesAutocomplete from 'react-google-places-autocomplete';

const far=(val)=>{
	return Math.round((val - 273) * 100) / 100;
}
var res
// import React from 'react';
const Temp= (props)=>{
	const [cur,setcur]=useState()
	const [city,setCity]=useState()
	const [temp,settemp]=useState()
	const [mtemp,msettemp]=useState()
	const [mmtemp,mmsettemp]=useState()
	const [cityad,setcityad] = useState("")
	
	const getJoke =async (char = 'Mumbai')=>{
			try{
			res = await axios.get(`https://api.openweathermap.org/data/2.5/weather?q=${char}&appid=92c2cd8e75c1490df8fa6d986051c9f0`)
			settemp(far(res.data.main.temp))
			msettemp(far(res.data.main.temp_min))
			mmsettemp(far(res.data.main.temp_max))
			tempf([temp,mtemp,mmtemp])
			}
			catch(e){
				settemp(-273)
			}
			try{
				const cit = await axios.get(`https://api.unsplash.com/search/photos?query=${char}&client_id=gjVYRN79EWcHf3cWRfYd77ihSmo9RqIf5piOSefxYKQ`)
				// console.log(cit.data.results[0].urls.full)
				// console.log(cit.data.results.length)
				var idxx = Math.floor(Math.random()*cit.data.results.length)
				if(cit.data.results.length>0) {
					// console.log(cit.data.results[0].urls.full)
					await setcityad(cit.data.results[idxx].urls.full)
					bgimg(cit.data.results[idxx].urls.full)
				}else{
					setcityad("")
				}
				}
				catch(e){
					setcityad("")
				}
				// console.log(cityad)
				
	}
	if(!temp){
		getJoke()
	}
	const changes=(event)=>{
		setcur(event.target.value)
	}

	const replay= async()=>{
		await setCity(cur)
		await getJoke(city)
		// setans(data[Math.floor(Math.random()*data.length)])	
	}
	const bgimg=(cityad)=>{
		props.bgimg(cityad);
	}
	const tempf=(temp)=>{
		props.ptemp(temp);
	}
	return (<>
<div>
<div style={{display:"flex", flexDirection:"row",alignItems:"center",justifyContent:'center'}} >
	<input list="city" name="city" type="text" onChange={changes}  placeholder="Mumbai" value={cur}/>
	<datalist id="city">
		<option value="London"/>
		<option value="Delhi"/>
		<option value="Tokyo"/>
		<option value="New york"/>
		<option value="Andheri"/>
		<option value="Kalyan"/>
	</datalist>

	<button onClick={replay} className='button_four' style={{margin:"5px"}}> <NavigateNextIcon/></button>
	</div>
	{
	(temp!==-273) ?
	<div style={{textAlign:'center',paddingTop:"10px"}}>
	<h4>{temp}&deg;C</h4>
	<small>&darr; {mtemp}&deg;C - {mmtemp}&deg;C &uarr;</small> 
	</div>
	
	:"City Not Found:{"
	}
	</div>
		
		
		</>
	)
	}

 export default Temp