import React,{useState} from 'react'
import bgimg from './Images/bg.jpeg'
import gitimg from './Images/github.png'
import linkimg from './Images/linkedin.png'

const Contact =()=>{
	const initial={
		name: '',
		email:'',
		suggestions:''
	}
	// const [store,setStore]=useState([])
	const [data,setData]=useState(initial)
	const changed=(event)=>{
		const {name, value} = event.target;
		setData((prevdata)=>{
			return{...prevdata,[name]:value};
		})
	}
	const formSubmit=async (event)=>{
		event.preventDefault();   
		if (data.name!=="" || data.email!==''){
		const {name,email,suggestions}=data;
		let rsp = await fetch(
		"https://reactjs-38092-default-rtdb.firebaseio.com/contacts.json",
				{ method:"POST",
				header:{"Content-Type": "application/json",},
				body:JSON.stringify({name,email,suggestions})
				})
		// setData(initial);
		alert(data.name + ", Submitted  Thanks for suggestions. We will soon work on it")}
		await setData(initial);
	}
	// console.log(store)
	return(<>
	<div style={{ backgroundImage: `url(${bgimg})`,
	backgroundRepeat: 'no-repeat',
	backgroundAttachment: "fixed",
	backgroundSize: "cover",
	repeat:"norepeat"}}>
	    <div className='main_div'>
		<div className='center_div' style={{height:"500px",boxShadow:"0px 0px  0px  0px ",backgroundColor:"white"}}>
		<br/>
		<h1>Contact Us</h1>
		<form style={{ padding:'8px', textAlign:'left', color:'black'}} onSubmit={formSubmit} method="POST">
		<div className="mb-3">
		<label >Name</label>
		<input name='name'type="text" className="form-control" onChange={changed} value={data.name} autoComplete="off" required/>
		</div>
		<div className="mb-3">
		<label for="exampleInputEmail1" className="form-label">Email address</label>
		<input name='email'type="email" className="form-control" onChange={changed} value={data.email} autoComplete="off" required/>
		</div>
		<div className="mb-3">
		<label for="exampleInputEmail1" className="form-label">Suggestions</label>
		<input name='suggestions' type="Text" className="form-control" onChange={changed} value={data.suggestions} autoComplete="off"/>
		</div>
		<button type="submit" className="btn btn-primary">Submit</button>   
		</form>
		<div style={{display:"flex", paddingLeft:"15px", paddingTop:"10px"}}> 
		<img onClick={() => window.open("https://www.linkedin.com/in/vaibhav-khandare-117513196")} style={{cursor: "pointer"}} src={linkimg} alt="linkedinimage" height='40px' weight='40px'/>
		&nbsp;&nbsp;&nbsp;		
		<img src={gitimg} onClick={() => window.open("https://github.com/VaibhavKhandare")} style={{cursor: "pointer"}} alt="githuimageimage" height='40px' weight='40px'/>
		</div>		
		</div>
		</div>
		</div>
		</>);
}

export default Contact